import React, { useContext, useRef, useState } from 'react'
import { UserContext } from '../../../store/UserContext'
import axios from 'axios'
import { Message } from '../../../Components/Message'
import { Loader } from '../../../Components/Loader'
import { TranslationContext } from '../../../store/TranslationContext'

const WebsiteAndSocials = () => {
    const {userData, setUserData} = useContext(UserContext)
    const config = {
        headers : {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${userData.token}`
        }
      }

    const [facebook, setFacebook] =  useState(userData.extention.facebook)
    const [instagram, setInstagram] =  useState(userData.extention.instagram)
    const [twitter, setTwitter] =  useState(userData.extention.twitter)
    const [linkedin, setLinkedin] =  useState(userData.extention.linkedin)
    const [youtube, setYoutube] =  useState(userData.extention.youtube)
    const [tiktok, setTiktok] = useState(userData.extention.tiktok)
    const [other_socials, setOtherSocials] = useState(userData.extention.other_socials)

    const [loading, setLoading] = useState(false)
    const [error, setError] = useState(false)

    const updateWebsiteAndSocials = async(e)=>{
        e.preventDefault()
        setLoading(true)
        try {
          const {data} = await axios.post(
            '/api/settings/update-socials/',
            {
                facebook : facebook.trim(),
                instagram : instagram.trim(),
                twitter :twitter.trim(),
                linkedin : linkedin.trim(),
                youtube : youtube.trim(),
                tiktok : tiktok.trim(),
                other_socials : JSON.stringify(other_socials),
                // page_website: page_website.trim(),
            },
            config) 
            const newData = {
                ...userData, 
                extention : {
                    ...userData.extention,
                     ...data
                }
            }
          setUserData(newData)
          localStorage.setItem('userData', JSON.stringify(newData))  
          setLoading(false)
          setError(null)
        }catch(error){
          setLoading(false)
          setError(error)
          console.log(error)
        }
        
      }
      const isDisabled = useRef(false)
      isDisabled.current = (facebook.trim() == userData.extention.facebook) && 
      (instagram.trim() == userData.extention.instagram) && 
      (tiktok.trim() == userData.extention.tiktok) && 
      (youtube.trim() == userData.extention.youtube) &&
      (linkedin.trim() == userData.extention.linkedin)
      const {translate}=useContext(TranslationContext)
  return (
    <div className="p-2">
        <div className="card d-flex flex-column p-3">
            <div className='flex-grow-1'>
                <div className="row">
                    <h4 className='mb-3 color-theme'>{translate('Socials')}</h4>
                    <div className="form-group gap-2 d-flex align-items-center col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-square-facebook fa-2xl color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control' maxLength={100} onChange={e=>setFacebook(e.target.value)} value={facebook || ''}/>
                    </div>
                    <div className="form-group gap-2 d-flex align-items-center col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-square-instagram fa-2xl  color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control' maxLength={100} onChange={e=>setInstagram(e.target.value)} value={instagram || ''}/>
                    </div>
                    <div className="form-group gap-2 d-flex align-items-center col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-tiktok fa-2xl  color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control' maxLength={100} onChange={e=>setTiktok(e.target.value)} value={tiktok || ''}/>
                    </div>
                    <div className="form-group gap-2 d-flex align-items-center col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-square-x-twitter fa-2xl  color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control'  maxLength={100} onChange={e=>setTwitter(e.target.value)} value={twitter || ''}/>
                    </div>
                    <div className="form-group gap-2 d-flex align-items-center  col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-square-youtube fa-2xl color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control' maxLength={100} onChange={e=>setYoutube(e.target.value)} value={youtube || ''}/>
                    </div>
                    <div className="form-group gap-2 d-flex align-items-center  col-12 col-sm-6 p-3">
                        <i className="fa-brands fa-linkedin fa-2xl color-theme"></i> <span className='fs-3'>/</span>
                        <input className='form-control' maxLength={100} onChange={e=>setLinkedin(e.target.value)} value={linkedin || ''}/>
                    </div>
                    <div>
                        {error && <Message removeAlert={()=> setError(null)} variant='danger' className='mt-3'>{error.response.data.detail || error.message}</Message>}
                    </div>
                
                    <div className="mt-3">
                        <div className="d-flex flex-row-reverse">
                            <button type="button" id="submit" name="submit"  className="btn btn-success m-2 d-flex gap-2" onClick={updateWebsiteAndSocials} disabled={error || loading || isDisabled.current}>
                                <span>{translate('Update Socials')}</span>{ loading && <Loader diam={23} />}
                            </button>
                        </div>
                    </div>
                </div> 
            </div>                
        </div>
    </div>
  )
}

export default WebsiteAndSocials